@import "../../globalVar.scss";

.leftnav {
  height: 100vh;
  width: 64px;
  flex: 1;
  margin: 0;
  padding: 0;
  border: 0;
  position: fixed;

  &__a {
    display: block;
    height: 100%;
    width: 100%;
  }

  &__button {
    height: inherit;
    width: inherit;
    color: $blueButton;
    background-color: $backgroundNav;
    border: 0 transparent;
    font-size: 2em;
    cursor: pointer;
    margin: 0;
    padding: 0;
    position: relative;
  }

  &__inner {
    position: absolute;
    height: 32px;
    width: 32px;
    top: 0;
    bottom: 0;
    margin: auto 8px;
  }

  &__button:hover {
    color: $focusButton;
  }
}
