@import "../../globalVar.scss";

.send-message {
  box-sizing: border-box;
  margin: 0 10px;
  padding: 5px 4px;
  border-top: solid 1px $grayLine;

  &__attach_btn {
    display: inline-block;
    height: inherit;
    width: inherit;
    color: $grayFont;
    background-color: $backgroundNav;
    border: 0 transparent;
    font-size: 1.4em;
    cursor: pointer;
    margin: 4px 5px;
    padding: 0;
    position: relative;
    vertical-align: middle;
  }

  &__block {
    display: inline-block;
    vertical-align: middle;
    width: calc(100% - 72px);
  }

  &__input {
    box-sizing: border-box;
    width: 100%;
    height: 32px;
    padding: 4px 5px;
    margin: 0;
    color: $blackFont;
    background-color: $backgroundInput;
    border: 1px solid $backgroundInput;
    border-radius: 30px;
    outline: none;
    transition: 0.3s;
    vertical-align: middle;
  }

  &__button {
    height: inherit;
    width: inherit;
    color: $blueButton;
    background-color: $backgroundNav;
    border: 0 transparent;
    font-size: 2em;
    cursor: pointer;
    margin: 0;
    padding: 0;
    position: relative;
    vertical-align: middle;
  }

  &__button:hover {
    color: $focusButton;
  }
}
